<template>
      <div id="threeOne" style="margin-left: -200px !important;width: 100vw !important;" ref="threeOne">

      </div>
</template>

<script>
    import * as THREE from "three";
    // import "three/examples/js/controls/OrbitControls";
    import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
    // import 'three/examples/js/controls/FirstPersonControls.js';
    import { FirstPersonControls } from 'three/examples/jsm/controls/FirstPersonControls.js';
    import { PointerLockControls } from 'three/examples/jsm/controls/PointerLockControls.js';
    export default{
      name:'threeOne',
      data() {
        return {
          key: 'value',
          scene:null,//场景
          camera:null,//相机
          renderer:null,//渲染实例
          cube:null,//渲染实例
          T0: new Date(),//上次时间
          point:null,//点光源
          cameraObj:{
            fov:100,
            near:10,
            far:1000,
          },
          cameraObj1:{
            clientX:0,
            clientY:0
          },
          mousedown:false,
          container:null,//节点
          controls:null,//控制器
          clock:null
        }
      },
      created() {

      },
      beforeDestroy() {

      },
      mounted() {
        this.container = this.$refs.threeOne;
        this.init()
        this.controls =new OrbitControls(this.camera, this.renderer.domElement);//创建控件对象
        this.controls.maxPolarAngle = Math.PI * 0.4;
        // this.controls.minDistance = 0;
        this.controls.maxDistance = 100;
        this.controls.enableKeys = true;
        // this.controls.autoRotate = true;

        this.animate()
      },
      methods:{
        onWindowResize() {

          this.camera.aspect = window.innerWidth / window.innerHeight;
          this.camera.updateProjectionMatrix();

          this.renderer.setSize( window.innerWidth, window.innerHeight );

          // this.controls.handleResize();

        },
        init(){
          console.log(window)
          let container = this.container;
          //创建场景
          this.scene = new THREE.Scene();
          this.scene.fog = new THREE.Fog( 0xffffff, 0, 750 );

          //创建摄像机
          this.camera = new THREE.PerspectiveCamera( this.cameraObj.fov, window.innerWidth / window.innerHeight, this.cameraObj.near, this.cameraObj.far );
          //摄像机向前移动
          this.camera.position.set(1000,1800,1000);
          // this.camera.lookAt(this.scene.position);//相机指向场景某物

          //创建渲染实例
          this.renderer = new THREE.WebGLRenderer({antialias: true, });// 抗锯齿
          //设置是否投射阴影
          this.renderer.shadowMap.enabled = true;
          this.renderer.shadowMap.type = THREE.PCFSoftShadowMap; // default THREE.PCFShadowMap
          //设置渲染窗口大小
          this.renderer.setSize( container.clientWidth, window.innerHeight );
          //将渲染节点推入页面节点数
          container.appendChild( this.renderer.domElement );

          this.clock = new THREE.Clock();

          this.addGeometry()
          this.addGeometry1()
          this.addGeometry2()
          this.addGeometry3()
          this.addPlant()
          this.directionalLight()
          this.commonSt()
          this.star()

          // this.controls = new FirstPersonControls(this.camera, this.renderer.domElement);//创建第一人称控件对象
          // this.controls.getObject().position.y = 50;
          // this.controls.getObject().position.x = 100;
          // this.scene.add( this.controls.getObject() );
          // this.controls.lookSpeed = 0.1; //鼠标移动查看的速度
          // this.controls.movementSpeed = 20; //相机移动速度
          // this.controls.noFly = true;
          // this.controls.constrainVertical = true; //约束垂直
          // this.controls.verticalMin = 1.0;
          // this.controls.verticalMax = 2.0;
          // this.controls.lon = -100; //进入初始视角x轴的角度
          // this.controls.lat = 0; //初始视角进入后y轴的角度
        },


        //公共环境设置
        commonSt(){
          // 辅助坐标系  参数250表示坐标系大小，可以根据场景大小去设置
          var axisHelpers = new THREE.AxisHelper(250);

          //环境光    环境光颜色与网格模型的颜色进行RGB进行乘法运算
          var ambient = new THREE.AmbientLight(0x444444);
          //点光源
          this.point = new THREE.PointLight(0xffffff);
          this.point.position.set(40, 20, 30); //点光源位置
          //场景中添加网格，默认添加坐标(0,0,0)
          this.scene.add(this.point,ambient,axisHelpers);
        },
        //星
        star(){
          var starGeometry = new THREE.Geometry(5,50,50)
          for(var i =0;i<10000;i++){
            var star=new THREE.Vector3()
            star.x = THREE.Math.randFloatSpread( 2000 );
            star.y = THREE.Math.randFloatSpread( 2000 );
            star.z = THREE.Math.randFloatSpread( 2000 );
            starGeometry.vertices.push(star);
          }
          //点材质
          var starsMaterial = new THREE.PointsMaterial( { color: '#888888',size:.1 } );
          //
          var starField = new THREE.Points( starGeometry, starsMaterial );
          this.scene.add(starField)
        },

        //方向光与阴影
        directionalLight(){
          // 方向光
          var directionalLight = new THREE.DirectionalLight(0xffffff, 1);
          // 设置光源位置
          directionalLight.position.set(60, 100, 40);
          this.scene.add(directionalLight);
          // 设置用于计算阴影的光源对象
          directionalLight.castShadow = true;
          // 设置计算阴影的区域，最好刚好紧密包围在对象周围
          // 计算阴影的区域过大：模糊  过小：看不到或显示不完整
          directionalLight.shadow.camera.near = 0.5;
          directionalLight.shadow.camera.far = 300;
          directionalLight.shadow.camera.left = -50;
          directionalLight.shadow.camera.right = 50;
          directionalLight.shadow.camera.top = 200;
          directionalLight.shadow.camera.bottom = -10;
          // 设置mapSize属性可以使阴影更清晰，不那么模糊
          directionalLight.shadow.mapSize.set(1024,1024)
          console.log(directionalLight.shadow.camera);
        },
        //创建平面几何
        addPlant(){
          //创建一个平面几何体作为投影面
          var planeGeometry = new THREE.PlaneGeometry(3000, 3000,10,10);
          var planeMaterial = new THREE.MeshLambertMaterial({
            color: 0x999999
          });
          var geometry=new THREE.Mesh(planeGeometry,planeMaterial)
          geometry.rotateX(-Math.PI / 2); //旋转网格模型
          // geometry.position.y = -50; //设置网格模型y坐标
          // 设置接收阴影的投影面
          geometry.receiveShadow = true;
          this.scene.add(geometry)
        },
        animate() {
          window.addEventListener( 'resize', this.onWindowResize, false );
        	let T1 = new Date();//本次时间
        	let t = T1-this.T0;//时间差
          let a=1
        	this.T0 = T1;//把本次时间赋值给上次时间
          // console.log(this.controls.maxPolarAngle)
          // console.log(this.controls.object)
          // if(this.controls.object.position.y<10)this.controls.object.position.y=10
        	//循环执行
        	requestAnimationFrame( this.animate );
        	// this.point.rotateY(a++);//旋转角速度0.001弧度每毫秒
        	//this.point.rotateX(a++);//旋转角速度0.001弧度每毫秒
        	this.cube.rotateY(0.001*t);//旋转角速度0.001弧度每毫秒
        	this.cube.rotateX(0.001*t);//旋转角速度0.001弧度每毫秒
        	this.cube.rotateZ(0.001*t);//旋转角速度0.001弧度每毫秒
          // this.camera.rotateY(0.001*t);//旋转角速度0.001弧度每毫秒
        	// this.camera.rotateX(0.001*t);//旋转角速度0.001弧度每毫秒
        	// cube.rotateZ(0.001*t);//旋转角速度0.001弧度每毫秒

          // var delta = this.clock.getDelta();
          // this.controls.update( delta );
          //更新控制器
          // this.controls.update();

        	this.renderer.render( this.scene, this.camera );
        },
        //立方体1
        addGeometry(){
          //创建一个几何体（长宽高）
          var geometry = new THREE.BoxBufferGeometry( 20,20,20,30,30,30 );
          //创建材质
          var material = new THREE.MeshBasicMaterial( { color: "#00ff00" } );
          //创建网格，包含几何体与材质
          this.cube = new THREE.Mesh( geometry, material );
          this.cube.add(this.addEdges(geometry,'#060606'))
          this.cube.position.x = -20;
          this.cube.position.y = 20;
          // this.cube.add(this.addLine(geometry,"#11a209"))
          // 设置产生投影的网格模型
          this.cube.castShadow = true;
          this.scene.add(this.cube);
        },
        //立方体2
        addGeometry1(){
          var geometry1=new THREE.SphereGeometry(5,50,50)
          // var material1=new THREE.MeshBasicMaterial({
          // var material1=new THREE.MeshLambertMaterial({//漫反射
          var material1=new THREE.MeshPhongMaterial({//镜面反射
            color:'#f8ea25',
            // opacity:0.5,
            // transparent:true
            // specular:"#00d5ff",
            // shininess:12
          })
          var cube1=new THREE.Mesh(geometry1,material1)
          cube1.position.set(20,20,0)
          // cube1.add(this.addEdges(geometry1,'#43897f'))
          cube1.castShadow = true;
          this.scene.add(cube1);
        },
        //立方体3
        addGeometry2(){
          var geometry2=new THREE.ConeGeometry(5,20,50)
          // var material1=new THREE.MeshBasicMaterial({
          var material2=new THREE.MeshLambertMaterial({//漫反射
          // var material1=new THREE.MeshPhongMaterial({//镜面反射
            color:'#00f8f0',
            // // opacity:0.5,
            // // transparent:true
            specular:"#00d5ff",
            shininess:1
          })
          var cube2=new THREE.Mesh(geometry2,material2)
          cube2.position.set(20,25,0)
          // cube2.add(this.addEdges(geometry2,'#895458'))
          this.scene.add(cube2);
        },
        //立方体4
        addGeometry3(){
          // 三维样条曲线CatmullRomCurve3创建一个曲线路径
          var curve = new THREE.CatmullRomCurve3([
            new THREE.Vector3(0, 0, 0),
            new THREE.Vector3(0, 100, 100),
            new THREE.Vector3(0, 0, 200),
            new THREE.Vector3(120, 0, 300),
            new THREE.Vector3(50, 50, 400),
            new THREE.Vector3(0, 0, 500),
            new THREE.Vector3(0, 0, 550),
            new THREE.Vector3(0, 100, 550),
            new THREE.Vector3(0, 500, 450),
            new THREE.Vector3(0, 600, 350),
            new THREE.Vector3(0, 400, 250),
            new THREE.Vector3(0, 300, 100),
            new THREE.Vector3(0, 130, 50),
            // new THREE.Vector3(0, 0, 0),
          ]);
          // 通过曲线生成一个管道几何体
          var geometry = new THREE.TubeBufferGeometry(curve, 200, 30, 30, true);
          // var geometry2=new THREE.ConeGeometry(5,20,50)
          // var material1=new THREE.MeshBasicMaterial({
          var material2=new THREE.MeshLambertMaterial({//漫反射
          // var material1=new THREE.MeshPhongMaterial({//镜面反射
            color:'#00f8f0',
            // // opacity:0.5,
            // // transparent:true
            specular:"#00d5ff",
            shininess:1
          })
          var cube2=new THREE.Mesh(geometry,material2)
          cube2.position.set(-20,50,50)
          // cube2.add(this.addEdges(geometry2,'#895458'))
          this.scene.add(cube2);
        },
        //创建线型
        addLine(geometry,color){
          var materialLine = new THREE.MeshBasicMaterial( { color } );
          var line = new THREE.Line(geometry, materialLine);
          return line
        },
        //描边
        addEdges(geometry,color){
         var edges = new THREE.EdgesGeometry( geometry );
         var edgesline = new THREE.LineSegments( edges, new THREE.LineBasicMaterial( { color } ) );
          return edgesline
        },
      }
    }
</script>

<style scoped lang="less">
</style>



